<template>
    <div>
        <div v-if="sidebar" class="animate__animated animate__fadeInLeft" style="position:absolute;z-index:100000;top:0;left:0;width:100vw;height:100vh;">
            <div style="height:100vh;width:100vw;" class="bg-green-600 p-8">
                <span class="typcn typcn-th-list text-4xl  text-white float-right" @click="sidebar=false"></span>
                <div style="min-height:100px;">
                </div>
                <div class="row ">
                  <div class="col-10">
                        <div @click="changePage(item.link)" class="border-b-2 my-2 border-green-300 text-white font-bold text-xl" v-for="(item, index) in listMenu" :key="index+'listMenu'" :to="item.link"
                        v-if="item.roles.includes($store.state.users.role)"
                        >
                            <span class="typcn text-4xl text-white" :class="`typcn-${item.icon}`"></span> {{item.title}}
                        </div>
                  </div>
                </div>
                <p class="text-white font-bold text-lg absolute" 
                style="bottom:30px;left:30px;"
                @click="logout"
                 >Logout <span class="typcn  ml-2 typcn-arrow-right"></span></p>
            </div>
        </div>
        <div class="p-8">
            <span class="typcn typcn-th-list text-4xl" @click="sidebar=!sidebar"></span>
            <span class="text-lg font-bold">
                {{$store.state.users.role}}
            </span>
            <span class="typcn typcn-bell text-4xl float-right" @click="$router.push('/notifikasi')"></span>
        </div>
        <router-view></router-view>
    </div>
</template>
<script>
import firebase from 'firebase'
let db = firebase.firestore();
export default{
    data(){
        return{
            sidebar:false,
            listMenu:[
                {
                    title:'Core Data',
                    link:'/admin/pks/core-data',
                    icon:'home',
                    roles:['Admin','SuperAdmin','Marketing','Security']
                },
                {
                    title:'Report Success',
                    link:'/admin/pks/report-success',
                    icon:'document-text',
                    roles:['Admin','SuperAdmin','Marketing','Security']
                },
                {
                    title:'Jadwal',
                    link:'/admin/pks/jadwal',
                    icon:'calendar',
                    roles:['Admin','SuperAdmin','Marketing','Security']
                },
                {
                    title:'Management User',
                    link:'/admin/pks/management-user',
                    icon:'user',
                    roles:['Admin','SuperAdmin','Marketing','Security']
                },
            ]
        }
    },
    mounted() {
        this.ceklogin();
    },
    methods: {
        changePage(data){
            this.$router.push(data)
            this.sidebar=false
        },
        logout(){
            firebase.auth().signOut().then(() => {
                console.log('logout berhasil')
                }).catch((error) => {
            });
        },
        ceklogin(){
            firebase.auth().onAuthStateChanged((users) => {
                console.log('users',users)
                if (users) {
                    db.collection(`users`).get().then(res=>{
                        let data = res.docs.map(doc=>doc.data())
                        data=data.filter(e=>e.uid==users.uid)
                        console.log('data user',data)
                        firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                            localStorage.setItem("auth._token.local", sdb.scramble('bearer '+idToken));
                            localStorage.setItem("auth.local", sdb.scramble('bearer '+idToken));
                            }).catch(function(error) {
                        });
                        if(data[0].changepassword=='true'){
                            this.$router.push('/auth/changepassword');
                        }
                        this.$store.commit("changeUsers", data[0]);
                        localStorage.setItem("users", JSON.stringify(data[0]));
                    })
                } else {
                    console.log('user sesion tidak ada')
                    this.$router.push('/auth/login')
                }
            });
        }
    },
    
}
</script>